import React, { useContext, useState, useEffect } from "react";
import { Profile, Loading } from "../components";
import { EditIcon } from "../components/profile/styles/profile";
import { USER_AND_ADDRESS, USERS, ADDRESS } from "../constants/apiRoutes";
import { Context } from "../store/Store";
import useSWR from "swr";
import * as ROUTES from "../constants/routes";
import axios from "../utils/axios";
import { mCPF, mNum } from "../utils/masks";
import { useHistory } from "react-router-dom";

export function ProfileContainer() {
  const { state, removeLocalStorage } = useContext(Context);
  const { user } = state;
  const history = useHistory();
  const [userAddress, setUserAddress] = useState();
  const [processing, setProcessing] = useState(false);
  const { data } = useSWR(`${USER_AND_ADDRESS}/${user.user_id}`);

  function handleUserInfo(e) {
    setProcessing(true);
    e.preventDefault();
    const obj = { user_id: data.user_id, full_name: userAddress.full_name };
    if (!data.document_number) {
      obj.document_number = userAddress;
    }
    axios
      .patch(USERS, { ...obj })
      .then((res) => {
        alert("salvo com sucesso");
        setProcessing(false);
      })
      .catch((error) => {
        // setError(error.message);
        setProcessing(false);
        console.log(error);
      });
  }

  function handleUserAddress(e) {
    setProcessing(true);
    e.preventDefault();
    const method = userAddress.address ? "patch" : "post";
    axios[method](USER_AND_ADDRESS, { id: "oi" })
      .then((res) => {
        alert("foi");
        setProcessing(false);
      })
      .catch((error) => {
        // setError(error.message);
        alert("foi não");
        setProcessing(false);
      });
  }

  function handleEditEmail(e) {
    e.preventDefault();
    history.push(ROUTES.EMAIL, { update: true });
  }

  function handleEditPhone(e) {
    e.preventDefault();
    history.push(ROUTES.SMS, { update: true });
  }

  useEffect(() => {
    setUserAddress(data);
  }, [data]);

  if (!data) return <Loading />;

  const isInvalidUser = !userAddress?.full_name || !userAddress?.document_number;
  const isInvalidAddress =
    !userAddress?.address?.street ||
    !userAddress?.address?.street_number ||
    !userAddress?.address?.district ||
    !userAddress?.address?.city ||
    !userAddress?.address?.state_code;

  console.log(data);

  return (
    <>
      <Profile>
        <Profile.Card>
          <Profile.Form>
            <Profile.Content>
              <Profile.Base onSubmit={handleUserInfo}>
                <Profile.Group marginbottomtitle={data.photo ? "" : "51"}>
                  <Profile.Title>Perfil</Profile.Title>
                  {data.photo && (
                    <Profile.data>
                      <Profile.Picture src={data.photo} />
                    </Profile.data>
                  )}
                  <Profile.Input
                    type="text"
                    placeholder="Nome"
                    value={userAddress?.full_name}
                    onChange={({ target }) => setUserAddress({ ...userAddress, full_name: target.value })}
                  />
                  <Profile.Input
                    disabled={data.document_number ? true : false}
                    placeholder="CPF"
                    maxLength="14"
                    value={userAddress?.document_number}
                    onChange={({ target }) => setUserAddress({ ...userAddress, document_number: mCPF(target.value) })}
                  />
                  <Profile.Div>
                    <Profile.Input disabled={true} type="email" placeholder="Email" value={data.email} />
                    <EditIcon size="24" onClick={handleEditEmail} />
                  </Profile.Div>
                  <Profile.Div>
                    <Profile.Input disabled={true} type="text" placeholder="Celular" value={data.phone_number} />
                    <EditIcon size="24" onClick={handleEditPhone} />
                  </Profile.Div>
                  <Profile.Submit disabled={isInvalidUser || processing}>Salvar Perfil</Profile.Submit>
                </Profile.Group>
              </Profile.Base>
              <Profile.Base onSubmit={handleUserAddress}>
                <Profile.Group marginbottomtitle={data.photo ? "139" : ""}>
                  <Profile.Title>Endereço</Profile.Title>
                  <Profile.Input
                    type="text"
                    placeholder="Rua"
                    value={userAddress?.address?.street}
                    onChange={({ target }) =>
                      setUserAddress({ ...userAddress, address: { ...userAddress.address, street: target.value } })
                    }
                  />
                  <Profile.Input
                    type="text"
                    placeholder="Numero"
                    value={userAddress?.address?.street_number}
                    maxLength="5"
                    onChange={({ target }) =>
                      setUserAddress({
                        ...userAddress,
                        address: { ...userAddress.address, street_number: mNum(target.value) },
                      })
                    }
                  />
                  <Profile.Input
                    type="text"
                    placeholder="Bairro"
                    value={userAddress?.address?.district}
                    onChange={({ target }) =>
                      setUserAddress({ ...userAddress, address: { ...userAddress.address, district: target.value } })
                    }
                  />
                  <Profile.Input
                    type="text"
                    placeholder="Cidade"
                    value={userAddress?.address?.city}
                    onChange={({ target }) =>
                      setUserAddress({ ...userAddress, address: { ...userAddress.address, city: target.value } })
                    }
                  />
                  <Profile.Input
                    type="text"
                    placeholder="Estado"
                    maxLength="2"
                    value={userAddress?.address?.state_code}
                    onChange={({ target }) =>
                      setUserAddress({
                        ...userAddress,
                        address: { ...userAddress.address, state_code: target.value.toUpperCase() },
                      })
                    }
                  />
                  <Profile.Input
                    type="text"
                    placeholder="Complemento"
                    value={userAddress?.address?.complement}
                    onChange={({ target }) =>
                      setUserAddress({ ...userAddress, address: { ...userAddress.address, complement: target.value } })
                    }
                  />
                  <Profile.Submit disabled={isInvalidAddress || processing}>
                    {userAddress?.address ? "Atualizar Endereço" : "Salvar Endereço"}
                  </Profile.Submit>
                </Profile.Group>
              </Profile.Base>
            </Profile.Content>
            <Profile.Group>
              <Profile.Submit
                onClick={() => {
                  removeLocalStorage();
                }}
              >
                Sair
              </Profile.Submit>
            </Profile.Group>
          </Profile.Form>
        </Profile.Card>
      </Profile>
    </>
  );
}
